import sportList from '../utils/sportList'
import leagueList from '../utils/leagueList'
import mongoose from 'mongoose'

const Schema = mongoose.Schema

const ProviderSchema = new Schema({
	sport: { type: String, enum: sportList },
	league: { type: String, enum: leagueList },
	apiLink: { type: String, required: true },
	bookmaker: { type: String, required: true },
	option: { type: Object },
	provider: { type: String, enum: [ 'jsonOdd', 'pickMon', 'none' ], required: true },
	updatedAt: { type: Date, required: true },
	activate: { type: Boolean, default: true }
})

const Provider = mongoose.model('Provider', ProviderSchema)

export default Provider